(function(global) {
  var audioContext = new (window.AudioContext || window.webkitAudioContext)();
  var analyser = audioContext.createAnalyser();

  let $selectBeep = $('#select-beep'),
      $addBeep = $('#add-beep');

  $addBeep.click(e => {
    $selectBeep.removeClass('hide');
  });

  $selectBeep.on('click', 'span', e => {
    let id = parseInt(e.target.textContent.trim(), 10) - 1;

    let $audio = $('#audio-'+id);
    if(!$audio.length) {
      $audio = $('<audio></audio>');
      let audio = $audio.get(0);
      audio.id = 'audio' + id;
      audio.src = location.pathname + '/beeps/' + id + '.mp3';
      audio.loop = true;
      $audio.on('canplay', e => {
        audio.play();
      });

      $(document.body).append($audio);
    }

    let beep = audioContext.createMediaElementSource($audio.get(0)),
        gain = audioContext.createGain();

    let newLine = {
      name: '[' + (id+1) + '] Beep ' + (panel.props.items.length + 1),
      oscillator: beep,
      gain: gain,
      connected: true,
      range: [0, 1],
      properties: {
        set volume(value) {
          if(typeof value !== 'number') return;

          gain.gain.value = value / 100;
        },
        get volume() {
          return parseInt(Math.round(gain.gain.value * 100));
        },
        types: {
          volume: {
            type: 'number'
          }
        }
      } 
    };

    beep.connect(gain);

    panel.setState({
      itemsStatus: panel.state.itemsStatus.concat(true)
    });
    panel.setProps({
      items: panel.props.items.concat(newLine)
    });

    $selectBeep.addClass('hide');
  });

  $('#add-oscillator').click(e => {
    let oscillator = audioContext.createOscillator(),
        gain = audioContext.createGain();

    let types = ['sine','square',
                 'sawtooth','triangle'];

    let newLine = {
      name: 'Oscillator ' + (panel.props.items.length + 1),
      oscillator: oscillator,
      gain: gain,
      connected: true,
      range: [0, 1],
      properties: {
        set frequency(value) {
          if(typeof value !== 'number') return;
          
          oscillator.frequency.value = value;
        },
        get frequency() {
          return oscillator.frequency.value;
        },
        set detune(value) {
          if(typeof value !== 'number') return;

          oscillator.detune.value = value;
        },
        get detune() {
          return oscillator.detune.value;
        },
        set type(value) {
          if(types.indexOf(value) === -1) return;

          oscillator.type = value;
        },
        get type() {
          return oscillator.type;
        },
        set volume(value) {
          if(typeof value !== 'number') return;

          gain.gain.value = value / 100;
        },
        get volume() {
          return parseInt(Math.round(gain.gain.value * 100));
        },
        types: {
          frequency: {
            type: 'number',
          },
          detune: {
            type: 'number',
          },
          type: {
            type: 'select',
            options: types
          },
          volume: {
            type: 'number'
          }
        }
      },

    };

    oscillator.start();
    oscillator.connect(gain);

    panel.setState({
      itemsStatus: panel.state.itemsStatus.concat(true)
    });
    panel.setProps({
      items: panel.props.items.concat(newLine)
    });
  });

  $('#add-stream').click(e => {
    navigator.getUserMedia({audio: true}, media => {
      let stream = audioContext.createMediaStreamSource(media),
          gain = audioContext.createGain();

      stream.connect(audioContext.destination);

      let record = new Recorder(stream, {
        workerPath: '/app/js/libs/recorderWorker.js'
      });

      record.record();

      setTimeout(() => {
        record.stop();

        record.getBuffer(buffers => {
          let buff = audioContext.createBuffer(2, buffers[0].length, audioContext.sampleRate);
          let bufferSource = audioContext.createBufferSource();

          buff.getChannelData(0).set(buffers[0]);
          buff.getChannelData(1).set(buffers[1]);

          bufferSource.buffer = buff;
          bufferSource.connect(gain);

          bufferSource.loop = true;

          bufferSource.start();

          let newLine = {
            name: 'Line In ' + (panel.props.items.length + 1),
            oscillator: bufferSource,
            gain: gain,
            connected: true,
            range: [0, 1],
            properties: {
              set volume(value) {
                if(typeof value !== 'number') return;

                gain.gain.value = value / 100;
              },
              get volume() {
                return parseInt(Math.round(gain.gain.value * 100));
              },
              types: {
                volume: {
                  type: 'number'
                }
              }
            }
          };

          panel.setState({
            itemsStatus: panel.state.itemsStatus.concat(true)
          });
          panel.setProps({
            items: panel.props.items.concat(newLine)
          });         
        });
      }, 10000);
    }, e => {});
  });

  global.audioContext = audioContext;
  global.analyser = analyser;
})(window);

(function(global) {
  let $canvas = $('#wave-canvas'),
      $wave = $('#wave');

  let canvas = $canvas.get(0).getContext('2d');

  let stop = false;

  function stopWave() {
     stop = true;
  }

  function drawWave() {
    let bufferLength = analyser.fftSize;
    let data = new Float32Array(bufferLength);

    let WIDTH, HEIGHT, SIDE_MARGIN,
          width, canvasWidth, canvasHeight;

    function setSizes() {
      WIDTH = $canvas.width() * 0.8;
      HEIGHT = $canvas.height() * 0.6;

      SIDE_MARGIN = $canvas.width() * 0.2;
      width = WIDTH * (1.0/bufferLength);

      canvasWidth = $canvas.width();
      canvasHeight = $canvas.height();
    }

    setSizes();
    $(window).resize(setSizes);


    (function loop() {
      if(stop) {
        stop = false;
        return;
      }
      requestAnimationFrame(function() {
        analyser.getFloatTimeDomainData(data);

        canvas.clearRect(0, 0, canvasWidth, canvasHeight);
        canvas.strokeStyle = '#D51A00';
        canvas.fillStyle = '#D51A00';
        canvas.beginPath();

        let x = SIDE_MARGIN/2;

        for(let i = 0; i < bufferLength; i++) {
          let value = data[i] * HEIGHT / 2;

          canvas.rect(x, HEIGHT*0.7 - value/2, width, value);

          x += width;
        }

        canvas.fill();

        loop();
      });
    })();
  }


  $canvas.attr('width', $wave.width());
  $canvas.attr('height', $wave.height());
  $(window).resize(function() {
    $canvas.attr('width', $wave.width());
    $canvas.attr('height', $wave.height());
  });

  global.drawWave = drawWave;
  global.stopWave = stopWave;
})(window);

(function(global) {
  let timeouts = [];

  let status = 0;

  const DURATION = 10*1000;

  function play() {
    let items = panel.props.items;

    let hasOn = items.some(function(a) {
      return a.connected;
    });

    if(status === 1 || !items.length || !hasOn) return;
    timeouts = [];
    status = 1;

    analyser.connect(audioContext.destination);
    drawWave();

    for(let item of items) {
      if(!item.connected) continue;
      let range = item.range;

      for(let i = 0, len = range.length; i < len; i+=2) {
        if(typeof range[i] === 'undefined') {
          i--;
          continue;
        }

        let pair = range.slice(i, i+2);
        timeouts.push(
          setTimeout(() => {
            item.gain.connect(analyser);
          }, pair[0]*DURATION),
          setTimeout(() => {
            item.gain.disconnect(analyser);
          }, pair[1]*DURATION)
        );
      }
    }

    timeouts.push(setTimeout(() => {
      stop();
      play();
    }, DURATION));
  }

  function stop() {
    if(status === 0) return;
    analyser.disconnect(audioContext.destination);
    let items = panel.props.items;
    for(let item of items) {
      item.gain.disconnect(analyser);
    }

    stopWave();
    for(let t of timeouts) {
      clearTimeout(t);
    }
    timeouts = [];
    status = 0;
  }


  $('#play').click(play);
  $('#stop').click(stop);
})(window);
